import React, { Component } from 'react';
import PreLoader, { apishka } from '../components/Api';
import Select from 'react-select';
import {NotificationContainer,NotificationManager} from 'react-notifications';
import {Card} from 'primereact/card';
import {AutoComplete} from 'primereact/autocomplete';

class Report extends Component {
	
	constructor(props){
		super(props);
		this.getData = this.getData.bind(this);
		this.getReportFile = this.getReportFile.bind(this);
		this.getSelectOptions = this.getSelectOptions.bind(this);

		
		this.state = {
			data:{},
			loading:false,
			id:this.props.match.params.id,
			params:[],
			inputs:{},
			selections:{}
		}
	}	
	
	componentDidMount(){	
		this.getData();		
	}	
	
	getData() {
		this.setState({loading:true},()=>{
			apishka(
				'GET', 
				{}, 
				{id:this.props.match.params.id}, 
				'/api/report', 
				(data) => {
					this.setState({data:data.outjson, loading:false, params:data.outjson.params})
				},
				(err) => {
					if (err.response)
						NotificationManager.error(err.response.data.message)
					else 
						NotificationManager.error('unknown error')
				}
			)
		})
	}
	
	getReportFile() {
		console.log(this.state.inputs)
		let params = '?'
		Object.keys(this.state.inputs).forEach((x) => {
			if (typeof(this.state.inputs[x]) === 'object')
				params += x +'=' + JSON.stringify(this.state.inputs[x]) + '&'
			else
				params += x +'=' + this.state.inputs[x] + '&'
			
		})
		params += 'filename=' + this.state.data.filename + '&template=..' + this.state.data.template_path
		window.open('/rep/'+this.state.data.path + params)
		
	}
	
	getSelectOptions(ptitle, substr, api_id) {
		this.setState({loading:true},()=>{
			apishka(
				'GET', 
				{}, 
				{id:api_id}, 
				'/api/methodinfo', 
				(data) => {
					apishka(
						data.outjson.methotypename, 
						{}, 
						{substr:substr}, 
						'/api/' + data.outjson.methodname, 
						(data) => {
							let selections = this.state.selections
							selections[ptitle] = data.outjson
							this.setState({selections:selections, loading:false})
						},
						(err) => {
							if (err.response)
								NotificationManager.error(err.response.data.message)
							else 
								NotificationManager.error('unknown error')
						}
					)
				},
				(err) => {
					if (err.response)
						NotificationManager.error(err.response.data.message)
					else 
						NotificationManager.error('unknown error')
				}
			)
		})
	}
	
	render() {
		return (
			<Card >
				<PreLoader on = {this.state.loading} />
				<h2>{this.state.data.title }</h2>
					
					{this.state.params.map((item) => {return (<div>
						{(() => {switch(item.typename) {
							case "select":
						        return (
									<div key = {item.ptitle + item.id}>
										<div><b>{item.ptitle}</b></div>
										<Select
											style = {{fontSize:'9pt'}}
											onChange = {(e) => {
												let inputs = this.state.inputs
												inputs[item.func_paramtitle] = e
												this.setState({inputs:inputs})
											}}
											value = {this.state.inputs[item.func_paramtitle]}
											
											onFocus = {(e) => this.getSelectOptions(item.ptitle,null,item.apimethod)}
											options = {this.state.selections[item.ptitle] || []}
											/>	
									</div>)
							case "multiselect":
						        return (
									<div key = {item.ptitle + item.id}>
										<div><b>{item.ptitle}</b></div>
										<Select
											style = {{fontSize:'9pt'}}
											isMulti
											onChange = {(e) => {
												let inputs = this.state.inputs
												inputs[item.func_paramtitle] = e
												this.setState({inputs:inputs})
											}}
											value = {this.state.inputs[item.func_paramtitle]}
											onFocus = {(e) => this.getSelectOptions(item.ptitle,null,item.apimethod)}
											options = {this.state.selections[item.ptitle] || []}	
										/>		
									</div>)									
							case "typehead":
								return (
									<div key = {item.title}>
										<div><b>{item.ptitle}</b></div>
										<div>	
											<AutoComplete 
												minLength={0} 
												placeholder={item.ptitle} 
												style = {{fontSize:'9pt'}}
												size={110} 
												field="label" 
												suggestions={this.state.selections[item.ptitle] || []}
												onClick = {(e) => e.target.value = null}
												completeMethod={(e) => this.getSelectOptions(item.ptitle, e.query, item.apimethod)}
												value = {this.state.inputs[item.func_paramtitle]}
												
												onChange={(e) => {if (e.value.value!==undefined) {
													let inputs = this.state.inputs
													inputs[item.func_paramtitle] = e.value.value
													this.setState({inputs:inputs})
												}}} />	
										</div>
									</div>)	
								default:
									return ( 
										<div>
											<div><b>{item.ptitle}</b></div>
												<input 	
													style = {{fontSize:'9pt'}}
													onChange = {(e) => {
														let inputs = this.state.inputs
														inputs[item.func_paramtitle] = e.target.value
														this.setState({inputs:inputs})
													}}
													value = {this.state.inputs[item.func_paramtitle]}
													type = {item.typename}
													className = {"form-control"}
													placeholder = {item.ptitle} />
										</div>			
									)										
								}})()}
							</div>	
						)}
					)}
					<button className = "btn btn-outline-success" onClick = {this.getReportFile}>Сформировать отчёт</button>
					
				<NotificationContainer/>		
			</Card>	
		);
	}
}

export default Report
