import {apishka} from '../Api';
import axios from 'axios';
import CryptoPro from 'crypto-pro';

import {NotificationManager} from 'react-notifications';	

const mdlpapi = 'http://94.230.251.68:1119/'
	
	export function MDLP_API(uri, itm, item){
		this.setState({loading:true},() => {
			let body = {}
			let args = {}

			
			apishka("GET", {}, {}, "/api/marksettings", 
				(data) => {
					data = data.outjson
					let user_id = data.user_id
					let client_id = data.client_id
					let client_secret = data.client_secret
					axios({
						method: 'POST',
						url: mdlpapi + 'auth',
						data: {
							client_id : client_id,
							user_id : user_id,
							auth_type : "SIGNED_CODE",
							client_secret : client_secret
						},
						params: {},
						withCredentials: true
					})
					.then(
						(response) => {
						const code = response.data.code
							CryptoPro.call('getCertsList').then( (certList) => {
								let certoptions = []
								certList.forEach((x) => certoptions.push({label:x.label, value:x.thumbprint}))
								this.setState({
									signmodal:true, 
									certoptions:certoptions,
									signcode:code					
								},() => {
									this.setState({
										signcallback:() => {
											CryptoPro.call('signData', this.state.signcert.value, window.btoa(this.state.signcode))
											.then( (signature) => {
												console.log("ITM:",itm, 'ITEM:',item)
								
												itm.parametrs.forEach((obj) => {
														
													if (obj.paramcolumn) 	
														body[obj.paramtitle] =	(item[this.state.config.filter((x)=> x.col === obj.paramcolumn.label)[0].title] 
															|| 
														 this.state.inputs[this.state.config.filter((x)=> x.col === obj.paramcolumn.label)[0].title]
														 || 
														 this.state.data[this.state.config.filter((x)=> x.col === obj.paramcolumn.label)[0].title]
														 || 
														 this.state.data[0][this.state.config.filter((x)=> x.col === obj.paramcolumn.label)[0].title]
														 )
													else {
														if (obj.paramconst === "_checked_")
															obj.paramconst = this.state.checked												
														body[obj.paramtitle] = obj.paramconst
													}
													
													if (obj.paramt && obj.paramt === 'mdlpsign')
														CryptoPro.call(
															'signData', 
															this.state.signcert.value, 
															window.btoa(body[obj.paramtitle]))
														.then( (signature) => body[obj.paramtitle] = signature)			
												})
												console.log("BODY:",body)	
												if (itm.actapitype === 'GET')  
													args = body					
												this.setState({loading:true},()=>		
													axios({
														method: 'POST',
														url: mdlpapi + 'token',
														data: {"code":this.state.signcode, "signature":signature},
														params: {},
														withCredentials: true
													}).then(
														(response) => {
															body["user_id"] = user_id
															axios({
																method: itm.actapitype,
																url: uri,
																data: body,
																params: args,
																withCredentials: true
															}).then(
																(response) => {
																	this.setState({loading:false})
																	if (itm.actapirefresh)
																			this.getData()
																	console.log(response)
																},
																(error) => {
																	if (error.response && error.response.data )
																		NotificationManager.error(error.response.data.message)
																	else
																		NotificationManager.error('unknown error')
																	this.setState({loading:false})
																}
															)
														}, 
														(error) => {
															if (error.response && error.response.data )
																NotificationManager.error(error.response.data.message)
															else
																NotificationManager.error('unknown error')
															this.setState({loading:false})
														}
													)
												)
											})
										}
									})							
								})
							});
							this.setState({loading:false})
						}, 
						(error) => {
							console.log( error )
							if (error.response && error.response.data )
								NotificationManager.error(error.response.data.message)
							else
								NotificationManager.error('unknown error')
							this.setState({loading:false})
						}
					);
				}, (err) => {
					if (err.response && err.response.data) {
						NotificationManager.error(err.response.data.message)
					}
					else {
						NotificationManager.error('unknown error!')
					}	
				}
			)
			
		})
	}