import React, { Component } from 'react';
import {Menubar} from 'primereact/menubar';
import {Link} from 'react-router-dom';

export class AppMenuBar extends Component {    

		r(model){
			model.forEach((x) => {
				let it = x
				
				while (it.childs > 0) {
					it = it.items
					this.r(it)
				}

				if (it.length)
					it.forEach((i) => {
						i.command = (e) => this.props.history.push(i.path)
					})
				else 
					it.command = (e) => this.props.history.push(it.path)
				
			})
		}
    render() {
		let model = this.props.model
		 
		this.r(model)
		/*model.forEach((x) => {
			let it = x
			
			while (it.childs > 0) {
				it = it.items
			}

			if (it.length)
				it.forEach((i) => {
					i.command = (e) => this.props.history.push(i.path)
				})
			else 
				it.command = (e) => this.props.history.push(it.path)
			
		})*/
        return <Menubar model = {model} />
	
    }
}