import React, { PureComponent } from 'react';
import PreLoader, { Api } from './Api';
import {NotificationContainer} from 'react-notifications';
import Gallery from 'react-grid-gallery';
import ListFilters from './List/ListFilters'
import ListPagination from './List/ListPagination'
import PageInfo from './List/PageInfo'
//import ListActs from './List/ListActs'
//import ListActsForEvery from './List/ListActsForEvery'
import * as methods from './List/ListMethods'
import * as g_methods from './Generic/Methods'
import Acts from './Generic/Acts'

const qs = require('query-string')

class Tiles extends PureComponent {
	
	constructor(props){
		super(props);
		this.getData = methods.getData.bind(this);
		this.onFilterChange = methods.onFilterChange.bind(this);
		this.getTable = methods.getTable.bind(this);
		this.setChecked = methods.setChecked.bind(this);
		this.delChecked = methods.delChecked.bind(this);
		this.onFilterChangeDate = methods.onFilterChangeDate.bind(this);
		this.pagination = methods.pagination.bind(this);
		this.delRow = methods.delRow.bind(this)
		this.queryBuilder = g_methods.queryBuilder.bind(this)
		this.queryBuilder2 = g_methods.queryBuilder2.bind(this)
		this.printQueryBuilder = g_methods.printQueryBuilder.bind(this)
		this.ifParse = g_methods.ifParse.bind(this)
		this.format = g_methods.format.bind(this)
		this.orderBy = methods.orderBy.bind(this)
		this.orderByDel = methods.orderByDel.bind(this)
		this.ColumnResizeEnd = methods.ColumnResizeEnd.bind(this)
		this.onColReorder = methods.onColReorder.bind(this)
		this.callApi = g_methods.callApi.bind(this)
		this.sendSocket = g_methods.sendSocket.bind(this)
		this.visibleCondition = g_methods.visibleCondition.bind(this);
		
		let path = null
		let inputs = null
		let nowdate = new Date()
		let search = null
		nowdate =  nowdate.getDate() + '.' + (nowdate.getMonth()+1) + "." + nowdate.getFullYear() + ' '+ nowdate.getHours() + ':' +  nowdate.getMinutes() + ':' + nowdate.getSeconds() 
		
		if (this.props.compo) {
			path = this.props.path
			inputs = this.props.inputs
			search = this.props.search
		} else {
			search = this.props.location.search
			inputs = qs.parse(this.props.location.search)
			path = this.props.match.params.path
		}

		this.state = {
			pagenum:1,
			pagesize:15,
			paramfilters:{},
			inputs:inputs,
			data:[],
			config:[],
			loading:true,
			acts:[],
			filters:[],
			kfilters:{},
			pagination:false,
			pagecount:0,
			foundcount:0,
			table:"",
			path:path,
			isorderby:false,
			ispagecount:false,
			ispagesize:false,
			isfoundcount:false,
			socket:false,
			nowdate:nowdate.toString(),
			search:search,
			classname:'',
			th:[],
			
			pgvis:'block',
			pgbtitle:'+',
			
			fltvis:'block',
			fltbtitle:'+',
			
			checked:[],
			
			colsOrder:[],
			colsSize:{}
		}
		
	}	
	
	componentDidMount(){
		this.getData();
	}
	
	componentWillReceiveProps(n){
		if (n.match)
			if (this.state.path !== n.match.params.path) 
				this.setState({path:n.match.params.path},this.getData())
			
		if (JSON.stringify(n.inputs) !== JSON.stringify(this.state.inputs) )
			this.setState({inputs:n.inputs},this.getData())
		
		if (n.compo && n.path !== this.state.path)
			this.setState({path:n.path, search:n.search},this.getData())
	}
	
	render() {
		return (
			<div>
				<PreLoader on = {this.state.loading} />
				<h3>{this.state.title}</h3>

				
				{(this.state.filters.length>0)?
					<ListFilters cntxt = {this} state = {this.state} />
				: 
					null	
				} 
				
				{(this.state.pagination)?
					<PageInfo cntxt = {this} state = {this.state} />
				: 
					null
				}
				
				<Acts state = {this.state} cntxt = {this} isforevery = {false} comp = 'list' />
				
				{this.state.data.map((item) => {return (<div className = "card" key = {'tt' + item.rownum}>
					<div className = {(this.state.classname || " ") + " row"}>
						<div className = {(this.state.classname || "col-md-11")} >
							{this.state.config.map((itm) => {  
								if (itm.visible){
									if (!itm.depency) {
										return (itm.type === "files" || itm.type === "file" || itm.type === "filelist")?
											<div key = {itm.col + itm.title} className = {itm.classname || "row"} style = {{ width:itm.width, paddingLeft:'25pt'}}>
												<b key = {itm.title}>{itm.title} </b> 
												<ul>
													{this.ifParse(item[itm.title]).map((x) => 
														<li key = {x.uri}><div className = "row">
															<div className = "col-md-5">{x.filename}</div>  
																<div className = "col-md-1">
																	<a target="_blank" rel="noopener noreferrer" href = {Api + "/schema/downloadfile?uri=" + x.uri + "&filename="+x.filename}>
																		<i className = "fa fa-link"></i>
																	</a>
																</div>
															</div>
														</li>)
													}
												</ul>
											</div>
										:
											(itm.type === "image" || itm.type === "images" || itm.types === "picture" || itm.type === "gallery")?
												<div className = {itm.classname || "row"} style = {{ width:itm.width, paddingLeft:'25pt'}}>
													<b key = {itm.title}>{itm.title} </b> 
													<Gallery
														enableLightbox={true}
														enableImageSelection={false}
														images={this.ifParse(item[itm.title])} />
												</div>	
											:
												(itm.type === "color" || itm.type === "colorpicker")?
													<div className = {itm.classname || "row"} style = {{ width:itm.width, paddingLeft:'25pt'}} ><b key = {itm.title}>{itm.title} </b> 
														<div style = {{backgroundColor:(item[itm.title] || "white")}}>								
														</div>	
													</div>	
												:
													<div key = {itm.title+item.rownum} className = {itm.classname || "row"} style = {{ width:itm.width, paddingLeft:'25pt'}}>
														<b key = {itm.title}>{itm.title}: </b> 
														<div key = {itm.col + itm.title} style = {{paddingLeft:'5px'}}>
															{this.format(item[itm.title])}
														</div>
													</div>														
																
									} else {
										let myitem = item[itm.title]
										if(myitem.length>0){
											return (
												<div key = {itm.col + itm.title} className = {itm.classname || "row"} style = {{ width:itm.width, paddingLeft:'25pt'}}>
													<b key = {itm.title}>{itm.title} </b> 
													<table>
														<thead>
															<tr>
																{Object.keys(myitem[0]).map((i)=>{return <th key = {i}>{i}</th>})}
															</tr>
														</thead>
														<tbody>
															{myitem.map((it) =>{ return (<tr key = {itm.col + JSON.stringify(it) + item.title}>
																{Object.keys(it).map((i)=>{return <td key = {i}>{it[i]}</td>})}
															</tr>)})}
														</tbody>				
													</table>
												</div>)
											}
										}	
								}
							})}
							
						</div>
						
						<Acts state = {this.state} item = {item}  cntxt = {this} isforevery = {true} comp = 'list' />
					</div></div>
				)})}
				
				<ListPagination cntxt = {this} state = {this.state} />
				<NotificationContainer/>			
			</div>	
		)
	}

}

export default Tiles
